import React from "react";
import {Switch,Route,Link } from "react-router-dom";
import{Layout,Typography,Space}from 'antd';
import {Navbar,Exchanges,Cryptocurrencies,News ,Homepage,CryptoDetails} from "./components";
import './App.css'
const App=()=>{
    return (<div className="app">
                    <div className="navbar">
<Navbar />
                    </div>
  <div className="main">
      <Layout>
           <div className="routes">
  <Switch>
             <Route exact path="/">
          <Homepage />
         </Route>
         <Route exact path="/exchanges">
          <Exchanges />
         </Route>
         <Route exact path="/Cryptocurrencies">
          <Cryptocurrencies />
         </Route>
         <Route exact path="/CryptoDetails">
          <CryptoDetails />
         </Route>
         <Route exact path="/news">
          <News />
         </Route>
  </Switch>
</div>
                     </Layout>
                    
                      <div className="footer">
   <Typography.Title  level={5} style={{color:'white',textAlign:'center'}}>
     Cryptoverse <br />
     This universe is created by <strong><i>Sanjay Shankar AJ</i> </strong>Studying @Dr.MCET<br/>
     All rights reserved
   </Typography.Title>
   <Space>
    <Link to="/">Home</Link>
    <Link to="/">Exchanges</Link>
    <Link to="/">News</Link>
   </Space>
                      </div>
                      </div>
    </div>);
}
export default App;